import React from "react"


class LiveOffers extends React.Component{
    render(){
        return(
            <div className="container-fluid">
                <div className="row">
                    <br/>
                    <div className="col l12 m12 s12 card" style={{padding:"0"}}>
                        <div className="col l4 m3 s4" style={{padding:"1% 0 0 2%"}}>
                            <img src="images/yorstore/s1.svg" height="80"/>
                        </div>
                        <div className="col l8 m9 s8" style={{padding:"0 0 0 15%"}}>
                            <div style={{fontSize:"12px",marginTop:"1%"}}>
                                <span><b>Barista</b><br/></span>
                                <span>Hangout, Bistupur<br/></span>
                                <span>Buy 2 Food Get 1 Free<br/></span>
                                <span>Valid Till : 20th Feb 2020</span>
                            </div>
                        </div>
                    </div>
                </div>
              
            </div>
        )
    }
}
export default LiveOffers