import React from "react"
import {Navbar,Icon,NavItem} from 'react-materialize'

class Menu extends React.Component{
    render(){
        return(
           <div className="container-fluid">
            <Navbar className="white" alignLinks="left" 
           
            brand={<a className="brand-logo" style={{top:"15%"}} href="#">
                <img src="images/yorapp-logo2.svg" width="100" alt='YorApp Logo'/>
                </a>
                } 
               
                menuIcon={<Icon className="black-text">menu</Icon>}
              
                    options={{
                    draggable: true,
                    edge: 'left',
                    inDuration: 250,
                    onCloseEnd: null,
                    onCloseStart: null,
                    onOpenEnd: null,
                    onOpenStart: null,
                    outDuration: 200,
                    preventScrolling: true
                    }}
                >
              
                <NavItem href="" className='black-text'>Getting started</NavItem>
                <NavItem href="components.html" className='black-text'> Components</NavItem>
                </Navbar>
            </div>
        )
    }
}

export default Menu