import React from "react"
import Swiper from "react-id-swiper"
import "swiper/css/swiper.css"
import "../css/banner.css"

const Pagination = () => {
    const params = {
        lazy: true,
        slidesPerView: 'auto',
        centeredSlides: true,
        spaceBetween: 30,
        pagination: {
          el: '.swiper-pagination',
          clickable: true
        }
    }
    return (
        
            <Swiper {...params} >
                <div><img className="imgLeft swiper-lazy " src="images/feed_slider_m/fs1.jpg"  alt="YS-BANNER"/></div>
                <div><img className="imgLeft swiper-lazy" src="images/feed_slider_m/fs2.jpg"  alt="YS-BANNER"/></div>
                <div><img className="imgLeft swiper-lazy" src="images/feed_slider_m/fs3.jpg"  alt="YS-BANNER"/></div>
                <div><img className="imgLeft swiper-lazy" src="images/feed_slider_m/fs4.jpg"  alt="YS-BANNER"/></div>
                <div><img className="imgLeft swiper-lazy" src="images/feed_slider_m/fs5.jpg"  alt="YS-BANNER"/></div>
                <div><img className="imgLeft swiper-lazy" src="images/feed_slider_m/fs6.jpg"  alt="YS-BANNER"/></div>
                <div><img className="imgLeft swiper-lazy" src="images/feed_slider_m/fs7.jpg" alt="YS-BANNER"/></div>
                <div><img className="imgLeft swiper-lazy" src="images/feed_slider_m/fs8.jpg"  alt="YS-BANNER"/></div>
                <div><img className="imgLeft swiper-lazy" src="images/feed_slider_m/fs9.jpg"  alt="YS-BANNER"/></div>
                <div><img className="imgLeft swiper-lazy" src="images/feed_slider_m/fs10.jpg"  alt="YS-BANNER"/></div>
            </Swiper>
     
     
    )
  };
  export default Pagination;